
@import url('https://fonts.googleapis.com/css?family=Julius+Sans+One|Lekton|Overpass+Mono|Rock+Salt|Ubuntu+Mono');
.task-div{
    background-color: #80b6e5;
    color: white;
}
.task {
    font-family: 'Overpass Mono', monospace;
}
.form {
    font-family: 'Overpass Mono', monospace;
}
.trash-icon{
    color: white;
}
.action-delete {
    cursor: pointer;
    opacity: .7;
    transition: all .3s linear;
}
.action-delete:hover {
    opacity: 1;
    color: red;
}



