
@import url('https://fonts.googleapis.com/css?family=Julius+Sans+One|Lekton|Overpass+Mono|Rock+Salt|Ubuntu+Mono');
.action {
  cursor: pointer;
  opacity: .7;
  transition: all .3s linear;
}
.board {
  color: white;
}
.panel-heading {
  background-color: #80b6e5;
}
.panel {
  background-color: #999999;
}
.action:hover {
  opacity: 1
}
.list-div {
  display: inline-block;
  vertical-align: top;
  width: 400px;
  margin-left: 15px;
  margin-right: 15px;
}
.form-list-plus {
  font-size: 200px;
  line-height: 500px;
  width: 368px;
  background-color: #999999;
  border: none;
}
.list-row {
  overflow: auto;
}
.list-wrapper {
  white-space: nowrap;
  min-height: 55rem;
}
.add-list-button {
  width: 400px;
}
.lister {
  font-family: 'Overpass Mono', monospace;
}
h1 {
  font-family: 'Rock Salt', cursive;
}
h3 {
  font-family: 'Overpass Mono', monospace;
}



